import React, {Component} from 'react';
import Table from './Table';
import Form from './Form';
import Api from './Api';

class App extends Component {
    constructor(props){
        super(props)

        this.state = {
            employees: [
                {
                    name: 'Charlie',
                    job: 'Janitor'
                },
                {
                    name: 'Mac',
                    job: 'Bouncer'
                },
                {
                    name: 'Dee',
                    job: 'Aspiring actress'
                },
                {
                    name: 'Dennis',
                    job: 'Bartender'
                }
            ]
        }
    }

    removeEmployee = index => {
        this.setState({
            employees: this.state.employees.filter((item, i) => {
                return i !== index
            })
        })
    }

    handleSubmit = employee => {
        this.setState({
            employees: [...this.state.employees, employee]
        })
    }

    render() {        
        const heading = <h1 className='site-heading'>Employee Lists</h1>

        return(
            <div className="container">
                <div className="app">
                    {heading}
                </div>

                <Table employeeList={this.state.employees} removeEmployee={this.removeEmployee}/>
                <Api />
                <Form handleSubmit={this.handleSubmit} />
            </div>
        );
    };
}

export default App