import React, {Component} from 'react';

class Table extends Component {
    render() {

        const {employeeList, removeEmployee} = this.props;

        const TableHeader = () => {
            return(
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Job</th>
                        <th></th>
                    </tr>
                </thead>
            );
        }

        const TableBody = props => {
            return(
                <tbody>
                    {props.employeeList.map((item, index) => (
                        <tr key={index}>
                            <td>{item.name}</td>
                            <td>{item.job}</td>
                            <td>
                                <button onClick={() => props.removeEmployee(index)}>Delete</button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            );
        }

        return(
            <table>
                <TableHeader />
                <TableBody employeeList={employeeList} removeEmployee={removeEmployee} />
            </table>
        );
    };
}

export default Table